  .notification-dropdown {
    position: fixed;
    top: 80px;
    left: 50%;
    right: auto;
    transform: translateX(-50%);
    width: calc(100vw - 32px);
    max-width: 360px;
    max-height: calc(100vh - 120px);
  }
  .notification-menu-container {
    position: relative;
  }
  .notification-btn {
    position: relative;
  }
  .notification-item {
    padding: 10px;
  }
  .notification-icon-wrapper {
    width: 28px;
    height: 28px;
  }
  .notification-icon {
    font-size: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .notification-header {
    padding: 12px 16px;
  }
  .notification-footer {
    padding: 10px 16px;
  }
}
.search-bar-container {
  position: relative;
  flex: 1;
  border-radius: 12px;
  max-width: 900px;
  margin: 0 auto;
}
.search-bar {
  width: 100%;
  display: flex;
  align-items: center;
  background: var(--brand-primary);
  border-radius: 50px;
