  overflow-y: auto;
  z-index: 1000;
  animation: dropdownFadeIn 0.2s ease;
}
.search-dropdown::-webkit-scrollbar {
  width: 6px;
}
.search-dropdown::-webkit-scrollbar-track {
  background: rgba(157, 138, 219, 0.1);
  border-radius: 3px;
}
.search-dropdown::-webkit-scrollbar-thumb {
  background: var(--brand-secondary);
  border-radius: 3px;
}
.search-dropdown::-webkit-scrollbar-thumb:hover {
  background: var(--brand-tertiary);
}
.search-group {
  padding: 8px 0;
}
.search-group:not(:last-child) {
  border-bottom: 1px solid rgba(157, 138, 219, 0.2);
}
.search-group-title {
  font-size: 12px;
  font-weight: 700;
  color: var(--text-secondary);
  text-transform: uppercase;
  padding: 8px 16px;
  letter-spacing: 0.5px;
}
.search-result-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px 16px;
  cursor: pointer;
  transition: background 0.2s ease;
}
.search-result-item:hover {
  background: rgba(157, 138, 219, 0.1);
}
.search-result-icon {
  width: 36px;
  height: 36px;
  border-radius: 8px;
  background: var(--brand-primary);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 16px;
  flex-shrink: 0;
}
.search-result-content {
  flex: 1;
  min-width: 0;
}
.search-result-title {
  font-size: 14px;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 2px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.search-result-subject {
  font-size: 12px;
  color: var(--text-secondary);
}
.no-search-results {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px 20px;
  color: var(--text-secondary);
}
.no-search-results .empty-search-icon {
  font-size: 48px;
  opacity: 0.3;
  margin-bottom: 16px;
}
.no-search-results p {
  font-size: 14px;
  margin: 0;
  text-align: center;
}
@media (max-width: 768px) {
  .search-bar-container {
    max-width: 100%;
  }
  .search-bar {
    padding: 8px 12px;
  }
  .search-bar input {
    font-size: 13px;
  }
  .search-dropdown {
    max-height: 300px;
  }
  .search-result-item {
    padding: 10px 12px;
  }
  .search-result-icon {
    width: 32px;
    height: 32px;
    font-size: 14px;
  }
